/* back to top button - start
================================================== */
.backtotop {
  right: 12px;
  z-index: 999;
  bottom: 50px;
  display: none;
  position: fixed;
}
.backtotop .scroll {
  z-index: 1;
  width: 50px;
  height: 50px;
  display: block;
  position: relative;
}
.backtotop .scroll:before,
.backtotop .scroll:after {
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  content: "";
  position: absolute;
  border-radius: 100%;
  background-color: #ffffff;
  transition: 0.6s cubic-bezier(0.25, 1, 0.5, 1);
  box-shadow: 0px 10px 60px 0px rgba(0, 0, 0, 0.2);
}
.backtotop .scroll:after {
  opacity: 0;
  transform: scale(1.5);
}
.backtotop .scroll:hover:before {
  opacity: 0;
  transform: scale(0.3);
}
.backtotop .scroll:hover:after {
  opacity: 1;
  transform: scale(1);
}
.backtotop .scroll i {
  left: 50%;
  z-index: 1;
  font-size: 15px;
  position: absolute;
  color: #c7a17a;
  transform: translateX(-50%);
  transition: 0.6s cubic-bezier(0.25, 1, 0.5, 1);
}
.backtotop .scroll i:nth-child(1) {
  top: 50%;
  transform: translate(-50%, -50%);
}
.backtotop .scroll i:nth-child(2) {
  top: 110%;
  opacity: 0;
  visibility: hidden;
}
.backtotop .scroll:hover i:nth-child(1) {
  top: -110%;
  opacity: 0;
  visibility: hidden;
  transform: translateX(-50%);
}
.backtotop .scroll:hover i:nth-child(2) {
  top: 50%;
  opacity: 1;
  visibility: visible;
  transform: translate(-50%, -50%);
}

