.pulse {
  z-index: 1;
  width: 40px;
  height: 40px;
  color: #ffffff;
  font-size: 10px;
  position: relative;
  margin-right: 20px;
  border-radius: 100%;
  align-items: center;
  display: inline-flex;
  justify-content: center;
  background-color: #c7a17a;
}
.pulse:before,
.pulse:after {
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  z-index: -1;
  content: "";
  position: absolute;
  border-radius: 100%;
  background-color: #c7a17a;
}
.pulse:before {
  animation: pulse 3s cubic-bezier(0.16, 1, 0.3, 1) infinite both;
}
.pulse:after {
  animation: pulse 3s 0.6s cubic-bezier(0.16, 1, 0.3, 1) infinite both;
}
.pulse i {
  margin-left: 2px;
}

@keyframes pulse {
  0% {
    opacity: 0.5;
    transform: scale(1);
  }
  100% {
    opacity: 0;
    transform: scale(2.1);
  }
}
